<template>
  <span>
    <tooltip-icon-button
      buttonSize="medium"
      btnIcon="mdi-table-column"
      btnTooltip="Configure table columns"
      :onClick="() => openDialog()"
    ></tooltip-icon-button>
    <v-dialog v-model="isOpen" max-width="500px">
      <header-selector-dialog
        v-if="isOpen"
        :grid-config-change="gridConfigChange"
        :closeDialog="closeDialog"
        :field-keys="fieldKeys"
        :tableName="tableName"
      ></header-selector-dialog>
    </v-dialog>
  </span>
</template>
<script>
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import HeaderSelectorDialog from './headerSelectorDialog.vue';
export default {
  components: { TooltipIconButton, HeaderSelectorDialog },
  props: ['fieldKeys', 'tableName', 'gridConfigChange'],
  data() {
    return {
      isOpen: false,
    };
  },
  methods: {
    closeDialog() {
      this.isOpen = false;
    },
    openDialog() {
      this.isOpen = true;
    },
  },
};
</script>
