<template>
  <v-card dense>
    <v-card-title>Header Selector</v-card-title>
    <v-card-text dense>
      <v-row dense>
        <v-col cols="6">
          <v-card dense outlined class="full-height">
            <v-card-text class="full-height">
              <v-list dense subheader two-line class="full-height">
                <v-subheader> Available columns </v-subheader>
                <draggable
                  v-model="availableFields"
                  :options="{ group: 'name' }"
                  style="min-height: 10px; height: 100%"
                >
                  <template v-for="(item, index) in availableFields">
                    <v-list-item
                      @dblclick="() => onItemSelect(item)"
                      dense
                      class="text-left header-selector-list-item"
                      selectable
                      :key="item.name"
                    >
                      <v-list-item-content>
                        <v-list-item-title v-text="index + 1 + '. ' + item.displayName"></v-list-item-title>
                        <v-list-item-subtitle v-text="item.name"></v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </draggable>
              </v-list>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="6">
          <v-card dense outlined class="full-height">
            <v-card-text class="full-height">
              <v-list dense two-line subheader class="full-height">
                <v-subheader> Selected columns </v-subheader>
                <draggable v-model="selectedFields" :options="{ group: 'name' }" style="min-height: 10px; height: 100%">
                  <template v-for="(item, index) in selectedFields">
                    <v-list-item
                      v-if="!item.isEditMode"
                      dense
                      class="text-left header-selector-list-item"
                      selectable
                      :key="item.name"
                    >
                      <v-list-item-content>
                        <v-list-item-title v-text="index + 1 + '. ' + item.displayName"></v-list-item-title>
                        <v-list-item-subtitle v-text="item.name"></v-list-item-subtitle>
                      </v-list-item-content>
                      <v-list-item-action>
                        <span>
                          <v-btn icon x-small color="green" @click="() => toggleEditMode(item, true)">
                            <v-icon>mdi-pencil</v-icon>
                          </v-btn>
                          <v-btn icon x-small color="red" @click="onItemDeselect(item)">
                            <v-icon>mdi-close</v-icon>
                          </v-btn>
                        </span>
                      </v-list-item-action>
                    </v-list-item>
                    <v-list-item
                      v-if="item.isEditMode"
                      dense
                      class="text-left header-selector-list-item"
                      :key="item.name"
                    >
                      <div class="text-right pb-2 pt-2">
                        <v-text-field dense v-model="item.displayName" label="Display Name" single-line></v-text-field>
                        <v-btn icon small color="green" @click="() => toggleEditMode(item, false)">
                          <v-icon>mdi-check</v-icon>
                        </v-btn>
                      </div>
                    </v-list-item>
                  </template>
                </draggable>
              </v-list>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="closeDialog"> Cancel </v-btn>
      <v-btn color="blue darken-1" text @click="saveConfig">Save Config</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as headerBuilderService from '../../services/headerBuilderService.js';
import * as gridTableHeadersService from '../../services/gridTableHeadersService';
import Draggable from 'vuedraggable';

export default {
  name: 'headerSelectorDialog',
  props: ['tableName', 'fieldKeys', 'closeDialog', 'gridConfigChange'],
  components: { Draggable },

  data() {
    return {
      fields: [],
      isFormFieldCreated: false,
      valid: false,
      availableFields: [],
      selectedFields: [],
    };
  },
  mounted() {
    this.fetchHeadersConfig();
    // this.mapsHeadersToFormFields();
  },
  methods: {
    fetchHeadersConfig() {
      return gridTableHeadersService.fetch(this.tableName).then(headerConfig => {
        this.mapsHeadersToFormFields(headerConfig);
        return headerConfig;
      });
    },
    mapsHeadersToFormFields(headerConfig = {}) {
      const savedHeaderConfigMap =
        (headerConfig &&
          headerConfig.hasOwnProperty('headers') &&
          headerConfig.headers &&
          headerConfig.headers.length > 0 &&
          headerConfig.headers.reduce((acc, curr) => {
            acc[curr.name] = curr;
            return acc;
          }, {})) ||
        {};

      this.fields = (this.fieldKeys || []).map(header => ({
        name: header,
        isEnabled:
          (savedHeaderConfigMap && savedHeaderConfigMap[header] && savedHeaderConfigMap[header].isEnabled) || false,
        displayName:
          (savedHeaderConfigMap && savedHeaderConfigMap[header] && savedHeaderConfigMap[header].displayName) ||
          headerBuilderService.extractNameFromField(header),
        isEditMode: false,
      }));

      this.availableFields = this.fields.filter(val => !val.isEnabled);
      this.selectedFields = this.fields.filter(val => val.isEnabled);

      this.isFormFieldCreated = true;
    },
    onItemDeselect(item) {
      this.selectedFields = this.selectedFields.filter(val => val.name != item.name);
      this.availableFields = this.availableFields.concat(item);
    },
    onItemSelect(item) {
      this.selectedFields = this.selectedFields.concat(item);
      this.availableFields = this.availableFields.filter(val => val.name != item.name);
    },
    toggleEditMode(item, value) {
      if (item) {
        item.isEditMode = value;
      }
    },
    saveConfig() {
      this.selectedFields = this.selectedFields.map(val => ({
        ...val,
        isEnabled: true,
      }));
      gridTableHeadersService
        .update(this.tableName, this.selectedFields)
        .then(() => {
          this.$toasted.show('Grid configuration saved');
          this.gridConfigChange();
          this.fetchHeadersConfig();
          this.closeDialog();
        })
        .catch(err => {
          this.$toasted.show('Grid configuration could not be saved', { type: 'error' });
        });
    },
  },
};
</script>
<style>
.header-selector-list-item:hover {
  background: #333;
  cursor: pointer;
}
.full-height {
  height: 100%;
}
</style>
